.App {
  .wp-block-group {
    &.text-large-padding {
      padding: 48px 0;
      margin-bottom: 96px;
    }

    &.text-large-indent {
      padding-left: 400px;
    }

    &.text-large-indent-reverse {
      padding-right: 400px;
    }
  }

  @media (max-width: 1199px) {
    .wp-block-group {
      &.text-large-indent {
        padding-left: 150px;
      }

      &.text-large-indent-reverse {
        padding-right: 150px;
      }
    }
  }

  @media (max-width: 991px) {
    .wp-block-group {
      &.text-large-indent {
        padding-left: 0;
      }

      &.text-large-indent-reverse {
        padding-right: 0;
      }
    }
  }

  .has-text-align-right {
    text-align: right;
  }
}
